import React from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import Header from '../components/Header';
import back from './images/back.png';
import './Style.css';

class Ranking extends React.Component {
  state = {
    hasRanking: false,
    ranking: [],
  }

  componentDidMount() {
    this.saveRankingOnState(this.getRanking());
  }

  getRanking = () => {
    return JSON.parse(localStorage.getItem('ranking'));
  }

  saveRankingOnState = (ranking) => {
    this.setState({
      ranking,
    }, () => this.orderRankingByScore());
  }

  orderRankingByScore = () => {
    const { ranking } = this.state;
    if (ranking !== null) {
      const sortedRanking = ranking.sort((a, b) => b.score - a.score);
      this.setState({
        hasRanking: true,
        ranking: sortedRanking,
      });
    } else {
      this.setState({ hasRanking: false });
    }
  }

  render() {
    const { ranking, hasRanking } = this.state;
    return (
      <>
        <Header />
        <h1 data-testid="ranking-title">Ranking</h1>
        <Link to="/game"><img src={ back } alt="back" /></Link>
        <div className="gameContainer">
          {hasRanking ?
          ranking.map(({ name, score, gravatarHash }, index) => (
            <li className="ranking" key={ index }>
              <span>{ index + 1 }</span>
              <img
                src={ gravatarHash }
                alt="avatar"
              />
              <span data-testid={ `player-name-${index}` }>
                {name}
              </span>
              <span><strong>{`Score: ${score}`}</strong></span>
            </li>
            ))
          : <h2>Nobody Played Yet</h2>
          }
        </div>
      </>
    );
  }
}

const mapStateToProps = ({ player: { name, score, gravatarHash } }) => ({
  name,
  score,
  gravatarHash,
});

export default connect(mapStateToProps, null)(Ranking);
